*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: -webkit-fill-available;
}

body {
  height: 100vh;
  height: -webkit-fill-available;
  overflow: hidden;
}


#root{
  height: 100%;
}

/* classname NavLink */
.active > *:first-child{
  display: none;
}

.active > *:last-child{
  display: block;
}

.unactive > *:first-child{
  display: block;
}

.unactive > *:last-child{
  display: none;
}